/**
 *  
 * @returns {1 | 2 | 3} 
 *   - 1: viewport < 768px
 *   - 2: Pad 768px  viewport  1200px
 *   - 3: PC viewport > 1200px
 */
export const device = function device() {
  const { innerWidth } = window
  if (innerWidth < 768) return 1
  if (innerWidth > 1200) return 3

  return 2
}

/**
 * 
 * @param {JSON} target - 
 * @param {String} name - 
 * @param {Function} fn - 
 */
export const overload = function (target, name, fn) {
  let old = target[name]
  target[name] = function () {
    if (fn.length === arguments.length) {
      return fn.apply(this, arguments)
    } else if (typeof old === 'function') {
      return old.apply(this, arguments)
    }
  }
}

export const createOverload = function () {
  const fnMap = new Map()
  function overload() {}
}
