import { useNavigate } from 'react-router-dom'

/**
 * 
 * @param {Boolean} url - 
 * @param {Boolean} [useRouter=false] - 
 * @returns
 */
export const useJump = () => {
  const navigate = useNavigate()
  return (url, useRouter = false) =>
    useRouter ? navigate(url) : (window.location.href = url)
}
